<template lang="pug">
  #news.d-flex.justify-content-center.flex-column
    div.stamp.d-flex.justify-content-center.align-items-center News
    b-container.d-flex.justify-content-center.align-items-center
      b-row.flex-column.d-flex.justify-content-center.align-items-center
        b-col.information(
          v-for="(box, index) in paginatedBoxs"
          :key="index"
          :style="{maxWidth:'100%'}"
          @click="route(index)"
          ).p-3
          .d-flex.flex-wrap
            b-col.carditem1(cols="12" sm="5").py-3.d-flex.justify-content-center.align-items-center
              b-img.image(:src="box.src" :style="{width:'250px', zIndex:'2' }")
            b-col.carditem(cols="12" sm="7").d-flex.flex-column.justify-content-center
              h3(:style="{color:'white'}") {{ box.title }}
              p(:style="{color:'white'}") {{ (box.text.length > 30) ? box.text.substr(0,30) + '...' : box.text }}
              .tag.d-flex.mt-3
                h6(:style="{color:'white'}").mr-auto {{ box.time }}
    b-pagination-nav(
      v-model="currentPage"
      :total-rows="totalRows"
      :per-page="perPage"
      :number-of-pages="numberOfPages"
      :link-gen="linkGen"
      @change="onPageChanged"
      pills
      use-router
      align="center"
    )
    footer.text-center.d-flex.justify-content-center.align-items-center
            .copyright Copyright  2020 Peggy's Restaurant
            .socoal
              a(href='https://www.facebook.com/tsvts/')
                img(src="../../public/img/fb.png" :style="{width:'30px' }")
              a(href='https://www.facebook.com/tsvts/')
                img(src="../../public/img/ig.png" :style="{width:'30px' }")
              a(href='https://www.facebook.com/tsvts/')
                img(src="../../public/img/line.png" :style="{width:'30px' }")

</template>

<script>
export default {
  data () {
    return {
      boxs: [
        { link: '/dm', title: 'Tex-Mex', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_325.jpg', text: 'HOLAFRIDAYS2/25~4/20' },
        { link: '/where', title: '', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_326.jpg', text: '' },
        { link: '/story', title: '4/17', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_329.jpg', text: '88FRIDAYS' },
        { link: '/story', title: ' 4/21 ', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_328.jpg', text: '2020/4/21~5/18+...' },
        { link: '/story', title: '85', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_330.jpg', text: '2020/5/19-2020/6/29' },
        { link: '/story', title: '', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_331.jpg', text: '2020/6/2~7/31' },
        { link: '/story', title: '', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_332.jpg', text: '' },
        { link: '/story', title: '', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_333.jpg', text: '2020/7/1~8/31' },
        { link: '/story', title: '', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_334.jpg', text: '7/14~8/31' },
        { link: '/story', title: '/&', src: 'https://tgifridays.com.tw/images/thumbnails/lnee/thumb_157_335.jpg', text: '' }
      ],
      paginatedBoxs: this.boxs,
      perPage: 5,
      currentPage: 1,
      box: [
        { p1: '', p2: '2020/8/4~8/31', p3: ' TGI FRIDAYS/- 2FRIDAYS', p4: '$1980', p5: '$3880', p6: '10%', p7: '', p8: '2020/8/4-2020/8/9/375ml / 170', p9: 'Step1TGI FRIDAYS Taiwan IG@tgifridaystaiwan', p10: 'Step2FBIGTGI FRIDAYS ', p11: '10%', p12: '10%', p13: '', p14: '160(500ml)', src: 'https://tgifridays.com.tw/images/news/2020/07/30-tgif-fathersday-eventpage1.jpg' },
        { p1: '', p2: 'FRIDAYS7/14~8/31', p3: ' FRIDAYS', p4: '7 ', p5: ' NT$99', p6: '', p7: '', p8: ' ', p9: '2020/7/14-2020/7/23TGI FRIDAYS 200 ', p10: ' ', p11: '', p12: '', p13: '', p14: '', src: 'https://tgifridays.com.tw/images/news/2020/07/09-tgif-summermenu-eventpage1.jpg' }
      ]
    }
  },
  methods: {
    paginate (pageSize, pageNumber) {
      const boxsToParse = this.boxs.slice().reverse()
      this.paginatedBoxs = boxsToParse.slice(
        pageNumber * pageSize,
        (pageNumber + 1) * pageSize
      )
    },
    onPageChanged (page) {
      this.paginate(this.perPage, page - 1)
      window.scrollTo({ top: 0, behavior: 'smooth' })
    },
    linkGen (pageNum) {
      return pageNum === 1 ? '?' : `?page=${pageNum}`
    },
    route (index) {
      const i = this.currentPage === 1 ? index : index + 5 * (this.currentPage - 1)
      console.log(i)
      this.$router.push({ name: 'dm', params: { p2: this.box[i].p2, p3: this.box[i].p3, p4: this.box[i].p4, p5: this.box[i].p5, p6: this.box[i].p6, p7: this.box[i].p7, p8: this.box[i].p8, p9: this.box[i].p9, p10: this.box[i].p10, p11: this.box[i].p11, p12: this.box[i].p12, p13: this.box[i].p13, p14: this.box[i].p14, src: this.box[i].src } })
    }
  },
  computed: {
    totalRows () {
      return this.boxs.length
    },
    numberOfPages () {
      return parseInt(this.totalRows / this.perPage)
    }
  },
  mounted () {
    this.paginate(this.perPage, 0)
  }
}
</script>
